html, body, img, p, h1, h2, h3, header, footer, section, ul  {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 1rem;
	font: inherit;
	vertical-align: baseline;
}

article, footer, header, section {
	display: block;
}

body {
  line-height: 1;
  color: #4A4A4A;
	text-align: center;
	font-family: 'Lato-Light'
}

h1 {
  font-size: 2rem;
}

h2 {
	font-size: 1.5rem;
	font-family: 'Lato-Light'
}